.header {
  background-size:cover;
  background-repeat: no-repeat;
  background-position: right;
  background-image: url(../images/photos/main-mobile.jpg);




  @include OnTablet {
    background-image: url(../images/photos/main-tablet.jpg);
    background-position: center;
    background-size: cover;
  }

  @include OnDesktop {
    background-image: url(../images/photos/main.jpg);
    background-position: center;
  }

  &__logo{
    flex-grow: 1;
  }

  &__call{
    margin-right: 30px;
    text-decoration: none;

    @include OnTablet {
      margin-right: 40px;
    }

    @include OnDesktop {
      &::before{
        content: "+1 234 555-55-55";
        position: relative;
        top: -10%;
        transform: translate(-100%, 50%);

        width: max-content;
        display: block;
        padding-right: 12px;

        font-weight: 700;
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 2px;
        text-transform: uppercase;
        color: #fff;

        opacity: 0;
        pointer-events: none;
        transition: 1.5s;
      }
      &:hover::before{
        opacity: 1;
        pointer-events: all;
        transition: 1.5s;
      }
    }
  }

  &__content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      box-sizing: border-box;
      height: 100vh;
  }

  &__title{
    display: flex;
    justify-content: center;
    @extend %title;

    @include OnTablet {
      font-size: 64px;
    }
    @include OnDesktop {
      font-size: 80px;
    }
  }
}




